var graceChecker = require('@/common/graceChecker.js');
export default {
	data() {
		return {
			oldPhone: '',
			phone: '',
			code: '',
			second: 90,
			showText: true
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
	},
	onLoad() {
		var user = uni.getStorageSync('uerInfo');
		this.oldPhone = user.phone;
	},
	methods: {
		sendCode() {
			//发送验证码
			var _this = this;
			if (_this.phone == '') {
				_this.$tools.toast('请填写正确的手机号码')
				return false;
			}

			//发送验证码
			_this.$request.common
				.sendCode({
					phone: _this.phone,
					type: 1
				})
				.then(data => {
					_this.$tools.loadingHide();
					if (data.status == 200) {
						_this.showText = false;
						var interval = setInterval(() => {
							let times = --_this.second;
							_this.second = times < 10 ? '0' + times : times; //小于10秒补 0
						}, 1000);
						setTimeout(() => {
							clearInterval(interval);
							_this.second = 90;
							_this.showText = true;
						}, 90000);
					} else {
						_this.$tools.toast(data.msg)
					}
				})
				.catch(err => {
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});
		},
		formSubmit(e) {
			var _this = this;
			//定义表单规则
			var rule = [{
				name: 'phone',
				checkType: 'phoneno',
				errorMsg: '电话号码不正确'
			}, {
				name: 'code',
				checkType: 'string',
				checkRule: '6,6',
				errorMsg: '验证码6个字符'
			}];
			//进行表单检查
			var formData = e.detail.value;

			var checkRes = graceChecker.check(formData, rule);
			if (checkRes) {
				_this.$tools.loading('数据提交中')
				_this.$request.user
					.updPhone(formData)
					.then(data => {
						_this.$tools.loadingHide();
						if (data.status == 1) {
							var user = uni.getStorageSync('uerInfo');
							user.phone = _this.phone;
							uni.setStorage({ //将用户信息保存在本地  
								key: 'uerInfo',
								data: user
							})

							_this.$tools.toast('修改成功')
							uni.navigateTo({
								url: '/pages/member/details'
							});

						} else {
							_this.$tools.toast(data.msg)
						}
					})
					.catch(err => {
						_this.$tools.loadingHide();
						//消息异常
						_this.$tools.toast('数据加载异常')
					});
			} else {
				_this.$tools.toast(graceChecker.error)
			}
		}
	}
};
